import React from 'react'
import { Link } from 'react-router-dom'
import './Navbar.css'
import logo from '../Assets/logo002.png'


const Navbar = () => {
    return (
        <>
            {/* <!-- ======= Header ======= --> */}
            <header id="header" >
                <div className='d-none d-md-block'>
                    <div className='latest-div d-flex flex-row'>
                        <div className=''>
                            <ul className='d-flex flex-row list-unstyled text-white'>
                                <li>INTERNATIONAL</li>
                                <li>ALUMNI</li>
                                <li>FACULTY</li>
                                <li>SCHOOLS</li>
                            </ul>
                        </div>
                        <div className='admission-button ms-auto'>
                            <button type="button" className='btn text-dark'>ADMISSIONS</button>
                        </div>
                    </div>
                </div>

                {/* <div className="col-12"></div> */}
                <nav className="navbar navbar-expand-lg navbar-light" >
                    <div className="container">
                        <Link className='logo me-auto'><img src={logo} alt="logo" /></Link>
                        {/* <Link className="navbar-brand" to="#!"><span style={{color: "#5e9693"}}>Psycho</span><span style={{color: "#fff"}}>logist</span></Link> */}
                        <button className="navbar-toggler" type="button" data-bs-toggle="offcanvas"
                            data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar" aria-expanded="false"
                            aria-label="Toggle navigation">
                            <i className="fa fa-bars"></i>
                        </button>
                        <div className="offcanvas offcanvas-end" tabindex="0" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel">
                            <div class="offcanvas-header ">
                                <h5 class="offcanvas-title" id="offcanvasNavbarLabel"></h5>
                                <button type="button" class="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                            </div>
                            <div className='d-flex flex-column '>

                                <div className='d-none d-md-block ms-auto '>

                                    <ul className="navbar-nav ">
                                        <li className="nav-item extras-nav">
                                            <Link className="nav-link" to="#">NEWS</Link>
                                        </li>
                                        <li className="nav-item extras-nav">
                                            <Link className="nav-link " to="#">EVENTS</Link>
                                        </li>
                                        <li className="nav-item extras-nav">
                                            <Link className="nav-link" to="#">JOBS</Link>
                                        </li>
                                        <li className="nav-item extras-nav">
                                            <Link className="nav-link" to="#">CONTACT</Link>
                                        </li>
                                    </ul>
                                </div>
                                <div className='admission-button1 d-flex justify-content-center d-lg-none ms-auto'>
                                    <button type="button" className='btn1 text-dark'>ADMISSIONS</button>
                                </div>
                                <div className='offcanvas-body'>
                                    <ul className="navbar-nav ms-auto justify-content-end flex-grow-1 pe-3">
                                        <li className="nav-item">
                                            <Link className="nav-link" to="https://sethu.ac.in/">Home</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link " to="https://sethu.ac.in/best-engineering-colleges-in-madurai/">About</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to="https://sethu.ac.in/best-engineering-academy-in-madurai/">Campus</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to="https://sethu.ac.in/best-engineering-academy-in-madurai/">Academics</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to="https://sethu.ac.in/research/">Research</Link>
                                        </li>
                                    </ul>
                                </div>
                            </div>


                        </div>
                    </div>
                </nav>

            </header>
            <div className='container d-flex justify-content-center banner-end'>
                <div className='text-center'>
                    <h3>IQAC</h3>
                    <p>Home {">"} IQAC</p>
                </div>
            </div>
            {/* <!-- End Header --> */}
        </>
    )
}

export default Navbar

